.search-form {
  background-color: rgba(56, 76, 187, .953);
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: 10px 700px;
  display: flex;
}

.search-btn, .load-more {
  height: 38px;
  width: 110px;
  color: #080808;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #f1f2ab;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
}

.btn-svg {
  align-content: center;
  display: flex;
}

.load-more {
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 5px;
  margin: 10px auto 50px;
  font-size: 16px;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #fbff27;
}

.gallery {
  width: 1200px;
  flex-wrap: wrap;
  margin: auto;
  padding: 20px 0;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 30px);
  margin: auto auto 10px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .5);
}

.gallery-img {
  height: 160px;
  width: 100%;
  max-width: unset;
  object-fit: cover;
}

.info-item {
  text-align: center;
  flex-direction: column;
  font-size: 13px;
  display: flex;
}

.info {
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  font-size: 13px;
  display: flex;
}

.disabled, .disabled:hover {
  color: #303030;
  opacity: .8;
  background-color: #ccc;
}

